.display{
  max-width: 1000px;
  margin: auto;
}
.display1{
  display: flex;
  gap: 20px;
}
.imga{
  flex: 1;
  
}
.dispay{
  flex: 1;
  padding: 10px 0px;
  display: flex;
  text-transform: uppercase;
  flex-direction: column;
  gap: 10px;
}
.dispay .p{
  color: #da0202dc;
}
.dispay p .sec{
  text-decoration: line-through;
}
.dispay h1{
  text-transform: capitalize;
  font-size: 28px;
  text-align: justify;
  color: #3d3d3d;
}
.dispay h3{
  font-size: 24px;
  color: #fd8e8e;
}
.dispay p{
  font-size: 20px;
}
.dispay span{
  font-size: 20px;
}
.dispay span p{
  padding: 6px 8px;
  color: #ffffff;
  background: #d10303;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
}
.dispay button{
  background: #ffffff;
  padding: 20px 36px;
  color: #000000;
  max-width: max-content;
  text-transform: uppercase;
  border: 1px #000000 solid;
  cursor: pointer;
  font-size: 16px;
}
.bedcrum{
  display: flex;
  text-transform: capitalize;
  gap: 5px;
  margin: 20px 0px;
}

/**** PROD STYLING ****/


.div{
  display: flex;
  padding: 10px;
  border: #dbdbdb solid 1px;
}
.div img{
  width: 100%;
  height: 100%;
}
.prod{
  display: flex;
  gap: 10px;
}
.image_single{
  border: 1px solid #dbdbdb;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 3;
}
.image_single img{
  width: 100%;
  height: 450px;
  object-fit: cover;
}
.image_map{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-betweenj;
}

