.remain{
  max-width: 1100px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 20px 0px;
}
.rela{
  margin-bottom: 10px;
  border: 1px solid #f5f5f5;
  padding: 5px;
  color: #ff7e7e;
}
.rela1{
  display: flex;
  gap: 20px;
  margin-top: 10px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  transition: all 0.5s ease;
  cursor: pointer;
  padding: 5px;
}
.rela1:hover{
  transform: scale(1.05);
}

.rel_img img{
  width: 50px;
}
.rel_del{
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.rel_del p{
  color: #616060;
}
.rel_del span{
  color: #ffb6b6;
}
.stars{
  color: #fff894;
  display: flex;
  gap: 2px;
  font-size: 14px;
}

@media screen and (max-width: 755px) {
  .remain{
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
  }
}