.related{
  margin: 30px 0px;
}
.related h2{
  color: #1d1d1d;
}
.relate{
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

@media screen and (max-width: 755px) {
  .relate{
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
  {
    padding: 10px 20px;
  }
}