.cate{
  margin-bottom: 30px;
}
.category{
  background-image: linear-gradient(to bottom,  rgba(43, 43, 43, 0.52), rgba(128, 14, 99, 0.322)), url("./cloth.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
  width: 100%;
  height: 38vh;
  margin-bottom: 30px;
}
.category h1{
  text-transform: capitalize;
  font-size: 40px;
  color: #ecea69;
}
.category h1:first-child{
  cursor: pointer;
  color: #ffffff;
  transition: all 0.4s ease-out;
}
.category h1:first-child:hover{
  color: #fff3cd;
}
.category h2{
  font-size: 50px;
  color: #ffffff;
}

.cat_grid{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  max-width: 1100px;
  gap: 20px;
  margin: auto;
}

.catid{
  padding: 20px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.card .img0{
  width: 100%;
  height: 250px;
  object-fit: cover;

}
.card .img1{
  display: none;
  width: 100%;
  height: 250px;
  object-fit: cover;
}
.card:hover .img0{
  display: none;
}
.card:hover .img1{
  display: flex;
}
.card_details h4{
  color: #252525;
  line-height: 22px;
}
.del_card{
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.del_card p{
  color: #ff5151;
}
.del_card span{
  color: #555555;
  text-decoration: line-through;
}

@media screen and (max-width : 750px) {
  .cat_grid{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    padding: 10px 20px;
  }
  
  
}