.shop{
  background-image: linear-gradient(to bottom,  rgba(43, 43, 43, 0.52), rgba(128, 14, 99, 0.322)), url("./store.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
  width: 100%;
  height: 38vh;
  margin-bottom: 30px;
}
.shop h1{
  font-size: 40px;
  color: #ecea69;
  text-transform: capitalize;
}
.shop .hm{
  color: #ffffff;
  transition: all 0.4s ease-out;
}
.shop .hm:hover{
  color: #fff3cd;
}
.shop span{
  font-size: 40px;
  color: #ffffff;
}
.shop_map{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  max-width: 1100px;
  margin: auto;
  gap: 20px;
}

.filter{
  max-width: 1100px;
  margin: auto;
  display: flex;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  padding: 10px 10px;
  justify-content: space-between;
}
.fil_bt{
  display: flex;
  gap: 20px;
}
.fil_btn p{
  color: red;
  text-transform: capitalize;
  font-size: 17px;
  cursor: pointer;
}
.fil_bt p{
  color: red;
  font-size: 17px;
  cursor: pointer;
  text-transform: capitalize;
}

@media screen and (max-width : 750px) {
  .shop_map{
    padding: 15px 20px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }
  .filter{
    margin: 10px 20px;
  }
  
}