.heroo{
  max-width: 1200px;
  margin: auto;
}
.hero1{
  height: 70vh;
  width: 100%;
  position: relative;
}
.hero1::after{
  content: "";
  position: absolute;
  width: 100%;
  background: #000000;
  height: 100%;
  z-index: 1;
  top: 0px;
  opacity: 0.2;
}
.hero_rap img{
 width: 100%;
 object-fit: cover;
}

.hero_details{
  position: absolute;
  color: white;
  top: 0px;
  display: flex;
  z-index: 2;
  margin: 90px;
  flex-direction: column;
  gap: 20px;
}
.hero_details h1{
  text-transform: capitalize;
  color: #fce45c;
}
.hero_details h2{
  text-transform: uppercase;
  font-size: 32px;
}
.hero_details button{
  padding: 20px 30px;
  background: #ffffff;
  max-width: max-content;
  border: solid 1px #000000;
  color: #000000;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.5s ease-out;
}
.hero_details button:hover{
  background: #fad6d6;
  color: #272727;
  text-decoration: underline;
}

@media screen and (max-width: 755px) {
  .hero1{
  
  }
  .hero_details h1{
    font-size: 20px;
  }
}