.nav{
  background: white;
  padding: 0px;
  position: sticky;
  top: 0px;
  z-index: 99;
  box-shadow: 1px 2px 1px rgba(0, 0, 0, 0.16);
}
.nav_top{
  background: #e20000d0;
  color: #ffffff;
  font-size: 14px;
  text-transform: capitalize;
}
.nav_top1{
  max-width: 1100px;
  margin: auto;
  display: flex;
  padding: 5px 0px;
  align-items: center;
  justify-content: space-between;
}
.navbar{
  max-width: 1100px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav_logo h1{
  font-size: 40px;
}
.nav_logo h1 span{
  color: #ff1d1d;
}
.nav_lists ul{
  display: flex;
  align-items: center;
  gap: 20px;
}
.nav_lists ul li{
  text-transform: capitalize;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.4s ease-out;
}
.nav_lists ul li:hover{
  color: #ff1d1d;
}
.nav_btn{
  display: flex;
  align-items: center;
  gap: 30px;
}
.nav_btn .login{
  width: 85px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  height: 35px;
  border-radius: 40px;
  cursor: pointer;
  background: #ffffff;
  border: 1px solid #000000;
}
.nav_btn .cart{
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.nav_sech{
  background: #ff1664;
  padding: 5px 0px;
}
.nav_sec1{
  max-width: 1100px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav_input{
  height: 40px;
  border-radius: 40px;
  display: flex;
  align-items: center;
  padding: 1px;
  justify-content: space-between;
  width: 500px;
  background: #ffffff;
}

.nav_input input{
  height: 100%;
  width: 100%;
  background: transparent;
  padding-left: 16px;
}
.nav_input input::placeholder{
  font-size: 15px;
  color: #000000;
}
.nav_input button{
  height: 100%;
  background: #ff1664;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  width: 100px;
}
.nav_icons{
  display: flex;
  align-items: center;
  gap: 20px;
}
.nav_icons button{
  width: 35px;
  color: #ffffff;
  font-size: 17px;
  height: 35px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid #ffffff;
}

@media screen and (max-width: 755px) {
  .nav_lists, .nav_sech,.nav_btn{
    display: none;
  }
  .nav_top {
    display: none;
  }
  .nav_top{
    padding: 5px 20px;
  }
  .navbar{
    padding: 10px 20px;
  }
}