.footer{
  background: #fffcfc;
  margin-top: 40px;
}
.foot1{
  background: #222222;
  padding: 30px 0px;
}
.foot_top{
  max-width: 1100px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.foot_top h2{
  color: #e4e4e4;
  text-transform: uppercase;
}
.letter{
  width: 500px;
  background: #ffffff;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1px;
  border-radius: 30px;
}
.letter input{
  padding-left: 20px;
  background: transparent;
}
.letter input::placeholder{
  font-size: 16px;
  color: #8f8f8f;
}
.letter button{
  background: #ff4646;
  height: 100%;
  font-size: 16px;
  padding: 0px 20px;
  border-radius: 30px;
  color: #ffffff;
  cursor: pointer;
}
.foot2{
  max-width: 1100px;
  margin: auto;
  display: flex;
  padding: 20px 0px;
  justify-content: space-between;
}
.fot_place{
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  font-size: 16px;
  color: #464646;
}
.fot_place a{
  color: #464646;
}
.fot h1{
  font-size: 40px;
}
.fot h1 span{
  color: #ff1d1d;
}
.fot1{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.fot1 h2{
  color: #464646;
}
.fot1 p{
  font-size: 16px;
  text-transform: capitalize;
  color: #747474;
  transition: all 0.5s;
  cursor: pointer;
}
.fot1 p:hover{
  color: #ff1d1d;
  transform: scaleY(1.1);
}
.foot3{
  background: #2e2e2e;
  padding: 5px 0px;
  text-align: center;
  font-size: 14px;
  text-transform: capitalize;
  color: #eeeeee;
  letter-spacing: 1px;
}

@media screen and (max-width: 755px) {
  .foot2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    text-align: center;
  }
  .foot_top{
    display: flex;
    gap: 20px;
    flex-direction: column;
  }
  .foot_top h2{
    font-size: 20px;
  }
  .letter{
    width: 300px;
    background: #ffffff;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1px;
    border-radius: 30px;
  }
}