.login{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}
.login h1{
  text-align: center;
  text-transform: uppercase;
  font-size: 22px;
  color: #ff4848;
  font-weight: 600;
}
.log{
  width: 340px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.log1{
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.log1 input{
  width: 100%;
  padding: 10px;
  border: 1px solid #f3f3f3;
}
.log1 input::placeholder{
  color: #aaaaaa;
}
.log1 label{
  font-size: 14px;
  color: #464646;
}
.log_btn button{
  padding: 10px;
  width: 100%;
  background: #ff4848;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  transition: 0.4s;
}
.log_btn button:hover{
  background: #ff1010;
}
.forgot{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.forgot p{
  font-size: 12px;
  color: #464646;
}
.forgot span{
  font-size: 14px;
  color: #ff4848;
  cursor: pointer;
}