.sponsor{
  width: 100%;
}
.spons{
  max-width: 1100px;
  margin: auto;
  padding: 0px 20px;
 
}
.spon img{
  width: 150px;
  height: 150px;
  object-fit: cover;
}
.slide_spoon{
  display: flex;
  gap: 20px;
}

@media screen and (max-width: 755px){
  .spon img{
    width: 50px;
    height: 50px;
    object-fit: cover;
  }
}