.baner{
  background-image: linear-gradient(to bottom,  rgba(43, 43, 43, 0.52), rgba(128, 14, 99, 0.322)), url("./cloth.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 55vh;
}
.banner {
  max-width: 1100px;
  display: flex;
  align-items: center;
  gap: 220px;
  margin: auto;
}
.bann1{
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.bann1 h1{
  text-transform: uppercase;
  color: #ffe4e4;
}
.bann1 h2{
  text-transform: capitalize;
  font-size: 23px;
  color: #eee675;
}
.ban_btn button{
  background: #ffffff;
  transition: all 0.3s ease-out;
  padding: 20px 28px;
  cursor: pointer;
  font-size: 18px;
  border: 1px solid #000000;
  color: #000000;
}
.ban_btn button:hover{
  background: #fa3232;
  border: 1px solid #ffffff;
  color: #ffffff;
}

@media screen and (max-width: 755px) {
  .banner{
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .bann1{
    padding: 20px;

  }
}