.cart{
  max-width: 900px;
  margin: auto;
}
.mapp{
  margin-top: 30px;
}
.ret{
  display: flex;
  gap: 50px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  padding: 10px;
  margin-bottom: 20px;
}
.shop_cart{
  text-align: center;
  margin: 80px 0px;
}
.shop_cart h1{
  text-transform: uppercase;
  color: #1d1d1d;
  font-size: 44px;
}
.shop_cart button{
  background: #ffffff;
  color: #000000;
  border: 1px solid #cccccc;
  padding: 20px 30px;
  font-size: 24px;
  border-radius: 55px;
  text-transform: uppercase;
  margin-top: 20px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.shop_cart button:hover{
  background: #ff0000;
  border: none;
  color: #f3f3f3;
}
.cart_img{
  padding: 20px;
  background: #f3f3f3;
  width: 300px;
}
.cart_img img{
  width: 100%;
  object-fit: cover;
}
.cart_det{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.cart_det h3{
  text-transform: capitalize;
  text-align: justify;
  color: #ff9494;
  line-height: 30px;
}
.cart_det h4{
  text-transform: uppercase;
  color: #525252;
}
.cart_det p{
  font-size: 18px;
  font-weight: 600;
  color: #fa34c8;
}
.cart_det .sub{
  color: #000000;
  font-size: 16px;
}
.fun{
  display: flex;
  align-items: center;
  gap: 30px;
  font-size: 20px;
}
.fun button{
  width: 40px;
  height: 40px;
  display: flex;
  font-size: 20px;
  align-items: center;
  justify-content: center;
  background: #f3f3f3;
  cursor: pointer;
}
.remove p{
  font-size: 24px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
  background: #ffffff;
  border: 1px solid #000000;
  border-radius: 50%;
  transition: 0.3s;
  cursor: pointer;
}
.remove p:hover{
  background: #ffffff;
  color: #ff0000;
}
.cart_total{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0px;
  text-transform: uppercase;
}
.check h1,.total h1{
  width: 300px;
  height: 100px;
  display: flex;
  color: #525252;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
}
.check h1{
  cursor: pointer;
  transition: 0.4s;
}
.check h1:hover{
  color: #ff9b9b;
}