.home_products{
  margin: 40px 0px;
}
.homeproduct{
  max-width: 1100px;
  margin: auto;
}
.home_head{
  margin: 20px 0px;
}
.home_head h2{
  text-transform: uppercase;
  background: #ffeded;
  text-align: center;
  color: #ff4545;
  font-size: 30px;
}
.home_head h3{
  text-align: center;
  margin-top: 8px;
  text-transform: capitalize;
  color: #353535;
}
.map{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

@media screen and (max-width: 755px) {
  .home_head{
    padding: 10px 20px;
  }
  .home_head h3{
    font-size: 15px;
  }
  .map{
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    padding: 10px 20px;
  }
 
}